              <thead>
                <tr>
                  <th class="text-left ma-1"><strong>Observations</strong></th>
                  <th class="ma-1"><strong>Recommendation</strong></th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td>
                    <strong
                      ><v-text-field
                        v-model="masonry.observation"
                        solo
                        class="ma-1 pa-0"
                      ></v-text-field
                    ></strong>
                  </td>
                  <td>
                    <strong
                      ><v-text-field
                        v-model="masonry.recommendation"
                        solo
                        class="ma-1 pa-0"
